$darkBase: #21252A;
$darkBaseFaded: #21252A55; 
$lightBase: #E9E9E9;
$lightBaseFaded: #E9E9E955;
$cardMinHeight: 307.3px;
$cardMaxHeight: 569px;
$cardMinWidth: 256px;
$cardMaxWidth: 474.02px;
$smallScreens: 360px;
$medScreens: 768px;
$largeScreens: 1440px;
$headerTextSize: 4rem;
$primaryTextSize: 1rem;
$smallestScreenSupported: 360px;



:root{
  --dark-base: #{$darkBase};
  --dark-base-faded: #{$darkBaseFaded};
  --light-base: #{$lightBase};
  --small-screens: #{$smallScreens};
  --med-screens: #{$medScreens};
  --large-screens: #{$largeScreens};
  --card-min-width: #{$cardMinWidth};
  --card-min-height: #{$cardMinHeight};
  --card-max-width: #{$cardMaxWidth};
  --card-max-height: #{$cardMaxHeight};
  --header-text-size: #{$headerTextSize};

}

